import service from '../../common/service.js';
import {
    mapState,
    mapMutations
} from 'vuex'

export default {
	onLoad(){},
	data() {
		return {
			phoneno:'psdemo2',
			password:'1qa2ws'
		};
	},
	methods: {
		...mapMutations(['login']),
	    bindLogin() {
			if (this.phoneno.length < 6) {
			     uni.showToast({
			        icon: 'none',
			        title: '账号最短为 6 个字符'
			    });
			    return;
			}
	        if (this.password.length < 6) {
	            uni.showToast({
	                icon: 'none',
	                title: '密码最短为 6 个字符'
	            });
	            return;
	        }
			this.$api.login([this.phoneno,this.password]).then((res)=>{
				this.toMain(res);
			})
	    },
		toMain(userName) {
		    this.login(userName);
		    /**
		     * 强制登录时使用reLaunch方式跳转过来
		     * 返回首页也使用reLaunch方式
		     */
		    uni.reLaunch({
		        url: '../index/index',
		    });
		}
	}
}
